import { ColumnDef, Row } from '@tanstack/react-table';
import user from '@/assets/imgs/user.png';
import { useNavigate } from 'react-router-dom';
import EditIcon from '@/components/icons/EditIcon';
import ViewIcon from '@/components/icons/ViewIcon';
import { Booking } from '@/features/booking/types/Booking.types';
import { cn } from '@/lib/utils';
import TrashIcon from '@/components/icons/TrashIcon';
import SyncIcon from '@/components/icons/SyncIcon';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<Booking> }) => {
    const navigate = useNavigate();

    return (
        <div className="me-6 text-end flex flex-row items-center justify-end gap-3">
            <button className="hover:bg-white-blue-300 rounded-full p-1">
                <SyncIcon className="text-primery-blue-400 cursor-pointer" width="20" height="20" />
            </button>
            <button className="hover:bg-white-blue-300 rounded-full p-1">
                <EditIcon className="text-primery-blue-400 cursor-pointer" />
            </button>
            <button className="hover:bg-white-blue-300 rounded-full p-1">
                <TrashIcon className="text-[#E81E1E] cursor-pointer" />
            </button>
        </div>
    );
};

export const columns: ColumnDef<Booking>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    ID
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-[#625E74] leading-[26.24px] font-semibold tracking-normal">
                {row.original.id}
            </div>
        ),
    },
    {
        accessorKey: 'rent',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    المستأجر
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-[#625E74] leading-[26.24px] font-semibold tracking-normal">
                {row.original.customer.full_name}
            </div>
        ),
    },
    {
        accessorKey: 'phone',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رقم الهاتف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-[#625E74] leading-[26.24px] font-semibold tracking-normal">
                {row.original.customer.phone}
            </div>
        ),
    },
    {
        accessorKey: 'car',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 text-nowrap !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم السيارة والموديل
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="flex flex-row justify-start items-center gap-4 text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                <img src={user} alt="" className="rounded-full w-10 h-10 object-cover" />
                <div className="flex flex-col gap-1">
                    <p className="tracking-normal text-primery-blue-900 font-medium text-sm text-nowrap">
                        {`${row.original.vehicle.brand} ${row.original.vehicle.brand_model}`}
                    </p>
                    <p className="tracking-normal text-white-blue-800 font-normal text-sm">
                        {' '}
                        {row.original.vehicle.make_year}
                    </p>
                </div>
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    المدفوع
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-[#625E74] leading-[26.24px] font-semibold tracking-normal">
                {row.original.total_price}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    المتبقي
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm px-4 py-3 !text-[#625E74] leading-[26.24px] font-semibold tracking-normal">
                    {/* {row.original.total_price} */}
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-sm px-4 text-nowrap py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    طريقة الدفع
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.payment_status}
                </div>
            );
        },
    },
    {
        accessorKey: 'service',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الحالة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-xs font-medium tracking-normal leading-[115%]">
                    <p
                        className={cn('p-1 w-fit rounded-sm', {
                            'bg-[#DAF8E6] text-[#1A8245]': row.original.status === 'completed',
                            'bg-[#FFFBEB] text-[#D97706]': row.original.status === 'pending',
                            'bg-[#FEEBEB] text-[#E10E0E]': row.original.status === 'rejected',
                        })}
                    >
                        {row.original.status}
                    </p>
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    إجراءات
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
