import { CartesianGrid, Line, LineChart, XAxis, YAxis } from 'recharts';
import {
    ChartConfig,
    ChartContainer,
    ChartLegend,
    ChartLegendContent,
    ChartTooltip,
    ChartTooltipContent,
} from '@/components/ui/chart';

interface Props {
    chartData: unknown[];
    chartConfig: ChartConfig;
    barLabelColor?: string;
}

const formatNumber = (value: any) => {
    if (value >= 1000000000) {
        return `${(value / 1000000000).toFixed(1)}B`; // Billions
    } else if (value >= 1000000) {
        return `${(value / 1000000).toFixed(1)}M`; // Millions
    } else if (value >= 1000) {
        return `${(value / 1000).toFixed(1)}K`; // Thousands
    } else if (value === 0) {
        return null;
    }
    return value; // Return the original value if less than 1000
};

export function ChartLineDefault({ chartConfig, chartData }: Props) {
    return (
        <div className="min-w-[508px] min-h-[316px]">
            {/* <CardContent> */}
            <ChartContainer config={chartConfig}>
                <LineChart
                    accessibilityLayer
                    data={chartData}
                    margin={{
                        left: 12,
                        right: 12,
                    }}
                >
                    <CartesianGrid vertical={false} />
                    <ChartLegend content={<ChartLegendContent />} />

                    <XAxis
                        dataKey="days"
                        tickLine={false}
                        axisLine={false}
                        tickMargin={8}
                        tickFormatter={(value) => value.slice(0, 3)}
                    />
                    <YAxis
                        axisLine={true}
                        tickLine={false}
                        tick={{ fill: '#6b7280', textAnchor: 'start' }}
                        // width={40}
                        tickFormatter={formatNumber}
                    />
                    <ChartTooltip cursor={false} content={<ChartTooltipContent hideLabel />} />
                    <Line dataKey="desktop" type="natural" stroke="var(--color-desktop)" strokeWidth={1} dot={true} />
                </LineChart>
            </ChartContainer>
            {/* </CardContent> */}
        </div>
    );
}
